* {
  font-family: 'Rubik', sans-serif;
  font-weight: 300;
  font-size: 20px; }

h1 {
  font-size: 70px;
  margin: 0; }

h2 {
  font-size: 30px;
  margin: 0; }

.home {
  height: 100vh;
  background: url("../../img/header.jpg") bottom center;
  background-size: cover;
  position: relative; }

.main {
  height: 500px; }

.h-name {
  position: absolute;
  top: 47%;
  left: 0;
  right: 0;
  margin: auto 0;
  transform: translateY(-50%); }

.h-name-container {
  display: inline-block;
  background-color: rgba(0, 0, 0, 0.2);
  padding: 0px 25px;
  margin: 5px 0;
  transition: 0.5s; }

.h-name-char {
  letter-spacing: 5px;
  display: inline-block;
  font-size: 70px;
  opacity: 1;
  transition: 0.5s; }

.h-name-char-hidden {
  opacity: 0;
  transform: scale(5); }

.h-profession {
  position: absolute;
  top: 90%;
  left: 0;
  right: 0;
  opacity: 1;
  transition: 1s; }

.h-profession-hidden {
  top: 100%;
  opacity: 0; }
